.character-sheet {
    min-height: 100vh;
    min-width: 100vw;
    padding: 25px;
    background-color: #666; /* ðŸ˜ˆ */
    color: #efefef;
    & > article {
        margin: auto;
        min-width: 800px;
        max-width: 1400px;
        padding: 25px;
        @media (min-width: 1200px) {
            padding: 3% 6%;
        }
        @media (min-width: 1500px) {
            padding: 5% 10%;
        }
    }
    .svg-as-img {
        filter: invert(99%) sepia(3%) saturate(384%) hue-rotate(237deg) brightness(112%) contrast(87%);
    }
}

.sheet-section {
    padding: 1em;
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.85);
    color: #444;
    svg {
        fill: #444;
    }
}

.equipment-list-container {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 90%;
    li {
        margin: 4px 0;
        padding: 10px;
        &:not(first-child) {
            border-top: 1px solid #ccc;
        }
        > div {
            display: inline;
        }
    }
}

.new-item-form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.5em;
}

.new-item-form {
    > * {
        margin: 4px;
    }
    .util-text-input label {
        display: none;
    }
}

.health-bar {
    height: 11px;
    width: 100%;
}

.health-bar[value] {
    -webkit-appearance: none;
    appearance: none;
}

.health-bar[value]::-webkit-progress-bar {
    background-color: #eee;
    border: 1px solid #eee;
    border-radius: 15px;
}

.health-bar[value]::-webkit-progress-value {
    border-radius: 15px;
}
.health-bar.good[value]::-webkit-progress-value {
    background-color: #05AC3A;
}
.health-bar.fair[value]::-webkit-progress-value {
    background-color: #ECB900;
}
.health-bar.poor[value]::-webkit-progress-value {
    background-color: #D82D2D;
}