.util-text-input {
    display: inline-block;
    padding: 10px;
    label {
        display: block;
        margin-bottom: 2px;
        font-size: 85%;
    }
    input, select {
        width: 100%;
    }
    &.block {
        display: block;
    }
    &.small {
        padding: 5px;
        font-size: 85%;
    }
}

.util-tooltip {
    position: relative;
    display: inline-block;
    &:hover {
        .util-tooltip-message {
            display: block;
        }
        .util-tooltip-icon {
            background-color: dodgerblue;
            color: #fff;
        }
    }
}

.util-tooltip-icon {
    display: inline-block;
    width: 14px;
    height: 14px;
    margin: 0 4px;
    padding-top: 1px;
    border-radius: 15px;
    background-color: #fff;
    color: dodgerblue;
    font-weight: bold;
    font-size: 12px;
    text-align: center;
    line-height: 1;
}

.util-tooltip-message {
    display: none;
    position: absolute;
    left: 100%;
    top: 0;
    min-width: 150px;
    padding: 4px;
    border-radius: 2px;
    border: 1px solid #bbb;
    background-color: #fff;
    box-shadow: 0 0 3px rgba(0,0,0,0.15);
    font-size: 93%;
}